.wrapper {
    background: #ECEFF1;
    display: flex;
    flex-flow: row wrap;
		margin: 0 auto;
		padding: 0 11% 10%;
}

.nav-wrapper {
	background-color: #008080;
	text-align: center;
	margin: 0 auto;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 160px;
  overflow: hidden;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 6px 9px gray;
  text-align: left;
}

.card > .img-container {
  width: 100%;
  height: auto;
  overflow: hidden;
  text-align: center;
} 
.card > .img-container img {
  margin-top: 10px;
  width: 100%;
  height: 140px;
} 

.cardImage {
  display: inline-block;
  border: 0;
  width: 200px;
  height: 220px;
}

.page-footer {
	position: fixed;
	width: 100%;
	bottom: 0;
	left: 0;
	text-align: center;
	padding: 10px;
	background-color: #008080;
}
